.ranking-container {
    margin: 50px 0; /* Adds 20px of space above and below each ranking */
  }


  .ranking-container p {
    margin-bottom: 20px; /* Adjust this to get the desired spacing */
  }
  
  .bold-text {
    font-weight: bold;
  }
  
  .ranking-score {
    font-size: 24px; /* Or any other size you find appropriate */
  }
  .ranking-image {
    width: 90%;  /* it will take the full width of its parent */
    height: auto; /* it will adjust its height automatically */
    object-fit: cover; 
  }
  